(function () {
    'use strict';
    module.exports = ['$scope', '$log', 'FastBill', '$state', function ($scope, $log, FastBill, $state) {
        FastBill.isAuthenticated().then(function (authenticated) {
            $log.debug('[MainCtrl] isAuthenitcated', authenticated);
            if (!authenticated) {
                $state.go('login');
            }
        });
    }];
}());
